/** @jsx jsx */
import { memo } from 'react';
import { jsx, css } from '@emotion/core';
import Icon from '@mdi/react';
import {
  mdiCodepen,
  mdiLinkedin,
  mdiMapMarker,
  mdiEmail,
  mdiBriefcase,
} from '@mdi/js';

const styles = {
  container: css`
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    align-items: center;
    grid-template-areas:
      'avatar'
      'name'
      'content';
    justify-items: center;
    padding: 2vmax;
    @media screen and (min-width: 600px) and (max-width: 1023px) {
      grid-template-rows: none;
      grid-template-areas: 'avatar name content';
      align-items: center;
    }
    @media screen and (min-width: 1024px) and (max-width: 1439px) {
      grid-template-rows: none;
      grid-template-areas: 'avatar name content';
      align-items: center;
    }
    @media screen and (min-width: 1440px) and (max-width: 1919px) {
      grid-template-rows: none;
      grid-template-areas: 'avatar name content';
      align-items: center;
    }
    @media screen and (min-width: 1920px) {
      grid-template-rows: none;
      grid-template-areas: 'avatar name content';
      align-items: center;
    }
  `,
  flexContainer: css`
    display: flex;
    justify-content: flex-start;
    align-items: center;
  `,
  flexDirectionColumn: css`
    flex-direction: column;
  `,
  content: css`
    grid-area: content;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
  `,
  icon: css`
    margin-right: 12px;
  `,
  avatar: css`
    grid-area: avatar;
    width: 15vmax;
    height: 15vmax;
  `,
  name: css`
    grid-area: name;
  `,
  titleFont: css`
    font-size: calc(10px + 1vmax);
  `,
  contentFont: css`
    font-size: calc(10px + 0.5vmax);
  `,
  hyperlink: css`
    text-align: left;
  `,
};

const InfoPanel = ({ data }) => {
  return (
    <div css={styles.container}>
      <div css={styles.avatar}>
        <img src={data.img} alt="avatar" width="100%" height="100%" />
      </div>
      <div
        css={[
          styles.flexContainer,
          styles.flexDirectionColumn,
          styles.name,
          styles.titleFont,
        ]}
      >
        <b>{data.name}</b>
        <div>{data.companyCh}</div>
        <div>{data.companyEn}</div>
      </div>
      <div css={[styles.content, styles.contentFont]}>
        <div css={styles.flexContainer}>
          <Icon path={mdiBriefcase} size={1} css={styles.icon} />
          <span>{data.jobTitle}</span>
        </div>
        <div css={styles.flexContainer}>
          <Icon path={mdiEmail} size={1} css={styles.icon} />
          <span>{data.email}</span>
        </div>
        <div css={styles.flexContainer}>
          <Icon path={mdiCodepen} size={1} css={styles.icon} />
          <a
            css={styles.hyperlink}
            href={data.codepen}
            target="_blank"
            rel="noopener noreferrer"
          >
            {data.codepen}
          </a>
        </div>
        <div css={styles.flexContainer}>
          <Icon path={mdiLinkedin} size={1} css={styles.icon} />
          <a
            css={styles.hyperlink}
            href={data.linkedIn}
            target="_blank"
            rel="noopener noreferrer"
          >
            {data.linkedIn}
          </a>
        </div>
        <div css={styles.flexContainer}>
          <Icon path={mdiMapMarker} size={1} css={styles.icon} />
          <span>{data.location}</span>
        </div>
      </div>
    </div>
  );
};

export default memo(InfoPanel);
