/** @jsx jsx */
import { memo, useMemo } from 'react';
import { jsx, css } from '@emotion/core';
import Item from './components/Item';

const ActivityPanel = ({ data }) => {
  const styles = useMemo(
    () => ({
      title: css`
        display: flex;
        justify-content: flex-start;
        font-size: calc(10px + 1.3vmax);
        font-weight: bold;
        border-bottom: 3px solid #9e9e9e;
        padding: calc(5px + 1vmax);
      `,
      content: css`
        display: grid;
        padding: calc(5px + 1vmax);
        font-size: calc(10px + 1vmax);
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: auto;
        grid-gap: calc(5px + 1vmax);
        @media screen and (max-width: 600px) {
          grid-template-columns: 1fr;
          grid-template-rows: repeat(2, auto);
        }
      `,
      external: css`
        display: flex;
        flex-direction: column;
      `,
      internal: css`
        display: flex;
        flex-direction: column;
      `,
      subTitle: css`
        font-weight: bold;
      `,
    }),
    []
  );

  return (
    <div>
      <div css={styles.title}>Activity</div>
      <div css={styles.content}>
        <div css={styles.external}>
          <span css={styles.subTitle}>外訓</span>
          {data.external.map((item, index) => (
            <Item
              key={`${item.text}-${index}`}
              text={item.text}
              subText={item.subText}
            />
          ))}
        </div>
        <div css={styles.internal}>
          <span css={styles.subTitle}>內訓</span>
          {data.internal.map((item, index) => (
            <Item
              key={`${item.text}-${index}`}
              text={item.text}
              subText={item.subText}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default memo(ActivityPanel);
