/** @jsx jsx */
import { memo, useMemo, useEffect, useState } from 'react';
import { jsx, css } from '@emotion/core';
import Paper from '@material-ui/core/Paper';
import Fade from '@material-ui/core/Fade';
import firebase from 'firebase';
import Lottie from 'react-lottie';
import InfoPanel from '../InfoPanel';
import SkillPanel from '../SkillPanel';
import ActivityPanel from '../ActivityPanel';
import EducationPanel from '../EducationPanel';
import ProjectPanel from '../ProjectPanel';
import JobPanel from '../JobPanel';
import { firebaseConfig } from '../../config';
import loadingJson from '../../assets/loading.json';

const Profile = () => {
  const styles = useMemo(
    () => ({
      container: css`
        display: grid;
        grid-template-areas:
          'info'
          'skill'
          'job'
          'education'
          'project'
          'activity';
        grid-template-columns: auto;
        grid-gap: 24px;
        padding: 24px;
        justify-content: center;
        width: 70vw;
        font-size: calc(10px + 0.5vmax);
        @media screen and (max-width: 425px) {
          width: 80vw;
        }
        @media screen and (min-width: 1920px) {
          font-size: calc(10px + 1vmax);
        }
      `,
      info: css`
        grid-area: info;
      `,
      education: css`
        grid-area: education;
      `,
      project: css`
        grid-area: project;
      `,
      job: css`
        grid-area: job;
      `,
      skill: css`
        grid-area: skill;
      `,
      activity: css`
        grid-area: activity;
      `,
      avatar: css`
        width: fit-content;
        height: fit-content;
      `,
      font: css`
        font-size: calc(10px + 0.5vmin);
      `,
    }),
    []
  );
  const [info, setInfo] = useState(null);
  const [education, setEducation] = useState(null);
  const [project, setProject] = useState(null);
  const [job, setJob] = useState(null);
  const [skill, setSkill] = useState(null);
  const [activity, setActivity] = useState(null);

  const isLoaded = useMemo(
    () => info && education && project && job && skill && activity,
    [activity, education, info, job, project, skill]
  );

  useEffect(() => {
    // Initialize Firebase
    firebase.initializeApp(firebaseConfig);
    firebase.analytics();

    const db = firebase.database();
    const storageRef = firebase.storage().ref();

    db.ref('/info').on('value', async (raw) => {
      const logoImgUrl = await storageRef.child('logo.svg').getDownloadURL();
      setInfo(() => ({
        ...raw.val(),
        img: logoImgUrl,
      }));
    });
    db.ref('/education').on('value', (raw) => {
      setEducation(raw.val());
    });
    db.ref('/activity').on('value', (raw) => {
      setActivity(raw.val());
    });
    db.ref('/project').on('value', (raw) => {
      setProject(raw.val());
    });
    db.ref('/skill').on('value', (raw) => {
      setSkill(raw.val());
    });
    db.ref('/job').on('value', (raw) => {
      setJob(raw.val());
    });
  }, []);

  return isLoaded ? (
    <Fade in timeout={3000}>
      <div css={styles.container}>
        <Paper elevation={1} css={styles.info}>
          {info && <InfoPanel data={info} />}
        </Paper>
        <Paper elevation={1} css={styles.education}>
          {education && <EducationPanel data={education} />}
        </Paper>
        <Paper elevation={1} css={styles.project}>
          {project && <ProjectPanel data={project} />}
        </Paper>
        <Paper elevation={1} css={styles.job}>
          {job && <JobPanel data={job} />}
        </Paper>
        <Paper elevation={1} css={styles.skill}>
          {skill && <SkillPanel data={skill} />}
        </Paper>
        <Paper elevation={1} css={styles.activity}>
          {activity && <ActivityPanel data={activity} />}
        </Paper>
      </div>
    </Fade>
  ) : (
    <Lottie
      options={{
        loop: true,
        autoplay: true,
        animationData: loadingJson,
        rendererSettings: {
          preserveAspectRatio: 'xMidYMid slice',
        },
      }}
      height={400}
      width={400}
      isStopped={false}
      isPaused={false}
      eventListeners={[
        {
          eventName: 'click',
          callback: () => console.log('the animation completed:'),
        },
      ]}
      isClickToPauseDisabled
    />
  );
};

export default memo(Profile);
