/** @jsx jsx */
import { memo, useMemo } from 'react';
import { jsx, css } from '@emotion/core';
import SkillContainer from './components/SkillContainer';

const SkillPanel = ({ data }) => {
  const styles = useMemo(
    () => ({
      title: css`
        display: flex;
        justify-content: flex-start;
        font-size: calc(10px + 1.3vmax);
        font-weight: bold;
        border-bottom: 3px solid #9e9e9e;
        padding: calc(5px + 1vmax);
      `,
      container: css`
        display: flex;
        flex-direction: column;
        height: 100%;
      `,
      content: css`
        flex: 1;
        display: grid;
        padding: calc(5px + 1vmax);
        grid-template-columns: auto;
        grid-template-rows: repeat(8, auto);
        grid-gap: calc(5px + 1vmax);
        @media screen and (min-width: 600px) and (max-width: 1023px) {
        }
        @media screen and (min-width: 1024px) and (max-width: 1439px) {
          grid-template-columns: repeat(2, auto);
          grid-template-rows: repeat(4, auto);
        }
        @media screen and (min-width: 1440px) and (max-width: 1919px) {
          grid-template-columns: repeat(3, 1fr);
          grid-template-rows: repeat(3, auto);
        }
        @media screen and (min-width: 1920px) {
          grid-template-columns: repeat(4, 1fr);
          grid-template-rows: repeat(2, auto);
          font-size: calc(10px + 0.7vmax);
        }
      `,
    }),
    []
  );

  return (
    <div css={styles.container}>
      <div css={styles.title}>Skill</div>
      <div css={styles.content}>
        {data.map((item) => (
          <SkillContainer
            key={item.skillClass}
            skillClass={item.skillClass}
            skills={item.skills}
          />
        ))}
      </div>
    </div>
  );
};

export default memo(SkillPanel);
