/** @jsx jsx */
import { memo, useMemo, useState, useCallback } from 'react';
import { jsx, css } from '@emotion/core';
import Paper from '@material-ui/core/Paper';
import Collapse from '@material-ui/core/Collapse';
import IconButton from '@material-ui/core/IconButton';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import Task from './components/Task';

const JobCard = ({ company, department, jobTitle, duringTime, children }) => {
  const [collapse, setCollapse] = useState(false);
  const handleCollapse = useCallback(() => {
    setCollapse((prevCollapse) => !prevCollapse);
  }, []);

  const styles = useMemo(
    () => ({
      container: css`
        padding: 1vmax;
        background: linear-gradient(
          rgba(217, 217, 217, 0.5),
          rgba(217, 217, 217, 0.5)
        );
      `,
      title: css`
        font-size: calc(10px + 0.6vmax);
        text-align: left;
      `,
      subheader: css`
        display: flex;
        justify-content: flex-start;
        font-size: calc(10px + 0.5vmin);
        color: gray;
      `,
      headerContainer: css`
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
      `,
      icon: css`
        transform: rotate(180deg);
        transition: transform 1s linear;
      `,
      iconReverse: css`
        transform: rotate(0deg);
        transition: transform 1s linear;
      `,
      content: css`
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 5px;
        justify-items: start;
      `,
    }),
    []
  );
  const isMobile = useMediaQuery('(max-width: 425px)');
  return (
    <Paper css={styles.container}>
      <div css={styles.headerContainer}>
        <div css={styles.titleContainer}>
          <b css={styles.title}>
            {isMobile ? company.slice(0, 3) : company} - {department}
          </b>
          <div css={styles.title}>{jobTitle}</div>
          <div css={styles.subheader}>{duringTime}</div>
        </div>
        <IconButton onClick={handleCollapse}>
          <ExpandMoreIcon css={collapse ? styles.icon : styles.iconReverse} />
        </IconButton>
      </div>
      <Collapse in={collapse}>
        <div css={styles.content}>{children}</div>
      </Collapse>
    </Paper>
  );
};

const JobPanel = ({ data }) => {
  const styles = useMemo(
    () => ({
      container: css``,
      title: css`
        display: flex;
        justify-content: flex-start;
        font-size: calc(10px + 1.3vmax);
        font-weight: bold;
        border-bottom: 3px solid #9e9e9e;
        padding: calc(5px + 1vmax);
      `,
      content: css`
        display: grid;
        grid-template-rows: repeat(3, auto);
        grid-row-gap: 12px;
        padding: calc(5px + 1vmax);
      `,
      font: css`
        font-size: calc(10px + 0.5vmin);
      `,
    }),
    []
  );
  return (
    <div css={styles.container}>
      <div css={styles.title}>Job</div>
      <div css={styles.content}>
        {data.map((item) => (
          <JobCard
            key={item.duringTime}
            company={item.company}
            department={item.department}
            jobTitle={item.jobTitle}
            duringTime={item.duringTime}
          >
            {item.tasks.map((task) => (
              <Task
                key={task.itemName}
                itemName={task.itemName}
                goals={task.goals}
              />
            ))}
          </JobCard>
        ))}
      </div>
    </div>
  );
};

export default memo(JobPanel);
